import React, { Component } from 'react'
import ConversationList from './ConversationList';
import { contacts } from '../mockup_data';

export default class Contacts extends Component {
  render() {
    return (
      <div>
        <h2>Kontakty</h2>
        <ConversationList conversations={contacts} />
      </div>
    )
  }
}
